/* navLargeLargebar CSS */
header.ui.secondary.pointing.menu {
  padding: 0 3rem;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  font-size: 2rem;
  transition: background-color 0.8s ease;
  z-index: 1000;
  background-color: #031424;
  height: 9.5rem;
  margin-bottom: 0;
  box-shadow: 0 0.2rem 3rem -5px rgba(0, 0, 0, 0.3);
}

/* navLargeLargebar width, margin, padding */
.contain {
  width: 100%;
  max-width: 120rem;
  margin: 0 auto;
  padding: 0 1.5rem;
}

/* Width, alignment, border, height for navLargeLargebar */
.navLarge {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: height 0.5s ease;
}
.navLarge .right.menu {
  height: 100%;
}

/* navLargebar links styling */
header.ui.menu.secondary .navLarge a.item {
  /* display: inline-block; */
  position: relative;
  font-size: 1rem;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1.6rem;
  height: 100%;
  align-self: center;
  color: #fff;
  transition: all 0.5s ease;
}
.right.menu:hover > a.item {
  -webkit-transform: scale(0.85);
          transform: scale(0.85);
}

header.ui.menu.secondary .navLarge a.item::after {
  content: "";
  display: block;
  width: 0;
  position: absolute;
  height: 2px;
  background: #fff;
  transition: width 0.5s;
  border: none;
  margin: none;
  -webkit-transform: none;
          transform: none;
  top: auto;
  bottom: 0;
  left: 0;
  visibility: visible !important;
}
header.ui.secondary.pointing.menu .navLarge a.item:hover::after {
  border-bottom: 2px solid #fff;
  width: 100%;
}
header.ui.secondary.pointing.menu .navLarge a.item:hover {
  background-color: #3effe387;
  color: #fff;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

